.nine-square-container {
    overflow: hidden;
    position: relative;
}

.nine-square-container, .im_wrapper {
    width: 100%;
    height: 100%;
}

.im_wrapper {
    position: absolute;
    overflow-y: scroll;
    overflow-x: hidden;
}

.nine-square-container .im_wrapper .default_ease_zoom:hover {
    z-index: 2;
    -webkit-transition: all 0s ease-in;
    -o-transition: all 0s ease-in;
    -webkit-transform: scale(1.1);
    -o-transform: scale(1.1);
    -ms-transition: all 0s ease-in;
    -ms-transform: scale(1.1);
    -moz-transition: all 0s ease-in;
    -moz-transform: scale(1.1);
    transition: all 0s ease-in;
    transform: scale(1.1);
}


.nine-square-container .im_wrapper div{
    left:-500px;
    width:125px;
    height:125px;
    position:absolute;
    background-repeat:no-repeat;
    background-color:#fff;
    cursor:pointer;
    -webkit-box-shadow:1px 1px 3px #000;
    box-shadow:1px 1px 3px #000;
}

.nine-square-container .curved {
    border-radius: 10px; /* future proofing */
}
.nine-square-container .im_wrapper div img{
    float:left;
    width:115px;
    height:115px;
    margin:5px 0 0 5px;
}
.nine-square-container .im_loading{
    display:none;
    position: relative;
    top: 50%;
    left: 50%;
    margin:-35px 0 0 -35px;
    width:70px;
    height:70px;
    z-index:9999;
}
.nine-square-container .im_next,
.nine-square-container .im_prev{
    width:50px;
    height:50px;
    display: inline-block;
    top: 50%;
    position:relative;
    cursor:pointer;
    opacity:0.7;
    z-index:1000;
    -webkit-box-shadow:0 0 3px #000;
    box-shadow:0 0 3px #000;
    border-radius:2px;
    filter:progid:DXImageTransform.Microsoft.Alpha(opacity=70);
}
.nine-square-container .im_next:hover,
.nine-square-container .im_prev:hover
{
    opacity:0.9;
    z-index: 2;
    -webkit-transition: all 200ms ease-in;
    -o-transition: all 200ms ease-in;
    -webkit-transform: scale(1.5);
    -o-transform: scale(1.5);
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1.5);
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1.5);
    transition: all 200ms ease-in;
    transform: scale(1.5);
}

@media screen and (max-width: 500px){
    .nine-square-container .im_wrapper div{
        left:-250px;
        width:65px;
        height:65px;
        position:absolute;
        background-repeat:no-repeat;
        background-color:#fff;
        cursor:pointer;
        -webkit-box-shadow:1px 1px 3px #000;
        box-shadow:1px 1px 3px #000;
    }

    .nine-square-container .im_wrapper div img{
        float:left;
        width:55px;
        height:55px;
        margin:5px 0 0 5px;
    }

    .nine-square-container .im_next,
    .nine-square-container .im_prev{
        width:25px;
        height:25px;
        display: inline-block;
        top: 50%;
        position:relative;
        cursor:pointer;
        opacity:0.7;
        z-index:1000;
        -webkit-box-shadow:0 0 3px #000;
        box-shadow:0 0 3px #000;
        border-radius:2px;
        filter:progid:DXImageTransform.Microsoft.Alpha(opacity=70);
    }
}

.nine-square-container .im_next{
    background:#fff url(../../images/next.png) no-repeat center center;
    left: 100%;
}
.nine-square-container .im_prev{
    background:#fff url(../../images/prev.png) no-repeat center center;
    left: -50px;
}

/*Pure CSS3 loader*/
@-webkit-keyframes line-scale-pulse-out {
    0% {
        -webkit-transform: scaley(1);
        transform: scaley(1);
    }
    50% {
        -webkit-transform: scaley(0.4);
        transform: scaley(0.4);
    }
    100% {
        -webkit-transform: scaley(1);
        transform: scaley(1);
    }
}

@keyframes line-scale-pulse-out {
    0% {
        -webkit-transform: scaley(1);
        transform: scaley(1);
    }
    50% {
        -webkit-transform: scaley(0.4);
        transform: scaley(0.4);
    }
    100% {
        -webkit-transform: scaley(1);
        transform: scaley(1);
    }
}

.nine-square-container .im_loading .line-scale-pulse-out > div {
    background-color: #47ECBA;
    width: 4px;
    height: 35px;
    border-radius: 2px;
    margin: 2px;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    display: inline-block;
    -webkit-animation: line-scale-pulse-out 0.9s -0.6s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
    animation: line-scale-pulse-out 0.9s -0.6s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
}

.nine-square-container .im_loading .line-scale-pulse-out > div:nth-child(2),
.nine-square-container .im_loading .line-scale-pulse-out > div:nth-child(4) {
    -webkit-animation-delay: -0.4s !important;
    animation-delay: -0.4s !important;
}

.nine-square-container .im_loading .line-scale-pulse-out > div:nth-child(1),
.nine-square-container .im_loading .line-scale-pulse-out > div:nth-child(5) {
    -webkit-animation-delay: -0.2s !important;
    animation-delay: -0.2s !important;
}



