.htmleaf-container{
    margin: 0 auto;
    text-align: center;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

#gallerybox {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
#gallerybox > span {
    display: none;
    position: absolute;
    right: 0;
    top: 0;
    padding: 1em;
    z-index: 1;
    font-family: Helvetica, Verdana, Arial, sans-serif;
    font-size: 12px;
    color: #ccc;
    cursor: pointer;
}

#gallerybox #gb-overlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

#gallerybox #gb-big {
    position: absolute;
}


#gallerybox img .thumbnail {
    display: block;
    padding: 4px;
    margin-bottom: 20px;
    line-height: 1.42857143;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
    -webkit-transition: border .2s ease-in-out;
    -o-transition: border .2s ease-in-out;
    transition: border .2s ease-in-out;
}

#gallerybox #gb-list {
    position: absolute;
    width: 100%;
    height: 150px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    left: 0;
    bottom: 0;
    padding: 0 20px;
    text-align: center;
    white-space: nowrap;
    overflow-x: hidden;
}
#gallerybox #gb-list:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}
#gallerybox #gb-list > .left,

#gallerybox #gb-list > .right {
    position: fixed;
    width: 20px;
    height: 150px;
    bottom: 0;
    line-height: 150px;
    font-family: Helvetica, Verdana, Arial, sans-serif;
    font-weight: 600;
    color: #ccc;
    background-color: rgba(51, 51, 51, 0.75);
    cursor: pointer;
}
#gallerybox #gb-list > .left {
    left: 0;
    border-radius: 0 100% 100% 0 / 0 50% 50% 0;
}
#gallerybox #gb-list > .right {
    right: 0;
    border-radius: 100% 0 0 100% / 50% 0 0 50%;
}

#gallerybox #gb-list > img {
    vertical-align: middle;
    margin: 0 1em;
    max-height: 75%;
    cursor: pointer;
}

.htmleaf-container img.gallerybox {
    width: 100%;
    max-width: 100%;
    height: auto;
}

.htmleaf-container #gallery-wrapper {
    position: relative;
    max-width: 75%;
    width: 75%;
    margin:50px auto;
}

.htmleaf-container .white-panel {
    position: absolute;
    background: white;
    border-radius: 5px;
    -webkit-box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
            box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
    padding: 10px;
}
.htmleaf-container .white-panel h1 {
    font-size: 1em;
}
.htmleaf-container .white-panel h1 a {
    color: #A92733;
}
.htmleaf-container .white-panel:hover {
    -webkit-box-shadow: 1px 1px 10px rgba(0,0,0,0.5);
            box-shadow: 1px 1px 10px rgba(0,0,0,0.5);
    margin-top: -5px;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}



